import React from "react";
import Button from "../elements/Button";
import { Fade } from "react-reveal";

export default function ProjectDetail(props) {
  //prevent scroll when modal is open
  if (props.isOpen) {
    document.body.style.overflow = "hidden";
  } else {
    document.body.style.overflow = "auto";
  }

  //get hidden className
  const className = props.className;

  if (!props.projectId) {
    return null;
  }

  //filter project data for modal
  let projectDetail = props.project.filter(
    (data) => data.id === props.projectId
  );

  return (
    <div
      className={[
        "fixed z-50 bg-black bg-opacity-50 w-full h-full overflow-auto mb-5",
        className,
      ].join(" ")}
    >
      <Fade bottom>
        <div className="w-9/12 max-w-lg m-auto mt-20">
          <div className="bg-white w-full h-10 text-right">
            <Button
              isDense
              type="link"
              className="font-bold text-3xl opacity-75 hover:opacity-100"
              onClick={props.closeProjectModal}
            >
              X
            </Button>
          </div>
          <div
            className={`${
              projectDetail[0].background || "bg-white"
            } select-none`}
          >
            {projectDetail[0].imageUrl ? (
              <img
                src={`%PUBLIC_URL%/images/project-display/${projectDetail[0].imageUrl}`}
                alt="firebnb img"
              />
            ) : (
              <p className="px-5 text-center">Load image...</p>
            )}
          </div>
          <div className="relative font-light bg-white">
            <div className="p-5 text-center pb-16">
              <h2 className="font-semibold text-lg">
                {projectDetail[0].title}
              </h2>
              <p className="text-sm">{projectDetail[0].desc}</p>
              <hr />

              <p className="text-justify mt-1 text-sm">
                {projectDetail[0].article}
              </p>
              {projectDetail[0].link && (
                <Button
                  className="absolute font-light right-0 bottom-0 hover:underline"
                  type="link"
                  isDense
                  isExternal
                  href={projectDetail[0].link}
                  target="_blank"
                >
                  Learn more &gt;
                </Button>
              )}
            </div>
          </div>
        </div>
      </Fade>
    </div>
  );
}
