import React from "react";
import Title from "../elements/Title";
import ProjectCard from "../elements/Card/Project";
import Button from "../elements/Button";
import { FaFileAlt } from "react-icons/fa";
import Fade from "react-reveal";

function Project(props, ref) {
  const project = props.project;
  return (
    <section className="px-3 py-12 mx-auto container" ref={ref}>
      <Title
        title="Project"
        caption="Here are few project I’ve created recently"
      />
      <div className="lg:flex lg:flex-wrap lg:justify-center">
        <Fade bottom>
          {project.map((project, index) => {
            let className = "";
            if (!project.featured) className = "lg:flex hidden";
            return (
              <ProjectCard
                key={index}
                desc={project.desc}
                className={className}
                id={project.id}
                openProjectModal={props.openProjectModal}
                getProjectId={props.getProjectId}
              >
                <img
                  src={`%PUBLIC_URL%/images/${project.logoUrl}`}
                  width="130rem"
                  alt={project.title}
                />
              </ProjectCard>
            );
          })}
        </Fade>
      </div>

      <Button
        isPrimary
        className="my-8 flex justify-center w-56 items-center mx-auto"
        isExternal
        type="link"
        target="_blank"
        rel="noopener noreferrer"
        href="https://drive.google.com/file/d/1XzXzeaDBWOq4pMWr3w1FskE3w5ntoC2j/view?usp=sharing"
      >
        <FaFileAlt />
        <span className="mx-1">Resume</span>
      </Button>
    </section>
  );
}

const refProject = React.forwardRef(Project);
export default refProject;
