import React from "react";
import SkillsetCard from "../elements/Card/Skillset";
import Title from "../elements/Title";
import {
  FaHtml5,
  FaFigma,
  FaReact,
  FaJs,
  FaNodeJs,
  FaCss3Alt,
  FaPhp,
  FaPython,
} from "react-icons/fa";
import { GrMysql } from "react-icons/gr";
import { DiMongodb } from "react-icons/di";
import Fade from "react-reveal";

function Skillset(props, ref) {
  return (
    <section
      className="text-white px-3 py-12 md:pt-40 md:pb-48  bg-black"
      ref={ref}
    >
      <div className="container mx-auto">
        <Title
          title="Tech &amp; Tools"
          caption="Here are several techs that I've used"
        />
        <div className="flex flex-wrap justify-center md:pt-6">
          <Fade bottom>
            <SkillsetCard
              title="HTML"
              skillsetIcon={<FaHtml5 size="1.85rem" />}
            />
            <SkillsetCard
              title="CSS"
              skillsetIcon={<FaCss3Alt size="1.85rem" />}
            />
            <SkillsetCard
              title="NodeJs"
              skillsetIcon={<FaNodeJs size="1.85rem" />}
            />
            <SkillsetCard
              title="ExpressJs"
              skillsetIcon={<FaJs size="1.85rem" />}
            />
            <SkillsetCard
              title="React"
              skillsetIcon={<FaReact size="1.85rem" />}
            />
            <SkillsetCard
              title="Figma"
              skillsetIcon={<FaFigma size="1.85rem" />}
            />
            <SkillsetCard title="Php" skillsetIcon={<FaPhp size="1.85rem" />} />
            <SkillsetCard
              title="Python"
              skillsetIcon={<FaPython size="1.85rem" />}
            />
            <SkillsetCard
              title="MySql"
              skillsetIcon={<GrMysql size="1.85rem" />}
            />
            <SkillsetCard
              title="MongoDB"
              skillsetIcon={<DiMongodb size="1.85rem" />}
            />
          </Fade>
        </div>
      </div>
    </section>
  );
}

const refSkillset = React.forwardRef(Skillset);
export default refSkillset;
